import React, { useState, useEffect } from "react";
import jsPDF from "jspdf";
import Logo from "./unica-logo.png"; // place your logo in src folder

export default function App() {
  const [rows, setRows] = useState(11);
  const [cols, setCols] = useState(3);

  const [grid, setGrid] = useState(
    Array(rows)
      .fill(0)
      .map(() => Array(cols).fill(null))
  );

  const [settings, setSettings] = useState({
    pageWidth: 210,
    pageHeight: 297,
    offsetX: 10,
    offsetY: 10,
    rowSpacing: 2,
    colSpacing: 2,
    cellWidth: 51,
    cellHeight: 21,
  });

  const [profiles, setProfiles] = useState({});
  const [selectedProfile, setSelectedProfile] = useState("");

  // Load profiles on mount
  useEffect(() => {
    const saved = JSON.parse(localStorage.getItem("labelProfiles") || "{}");
    setProfiles(saved);
  }, []);

  // Adapt grid size if rows/cols change
  useEffect(() => {
    setGrid((oldGrid) =>
      Array(rows)
        .fill(0)
        .map((_, r) =>
          Array(cols)
            .fill(0)
            .map((_, c) => (oldGrid[r] && oldGrid[r][c] ? oldGrid[r][c] : null))
        )
    );
  }, [rows, cols]);

  // Image file input
  const handleFileChange = (row, col, e) => {
    const file = e.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = () => {
      setGrid((oldGrid) =>
        oldGrid.map((r, ri) =>
          r.map((c, ci) => (ri === row && ci === col ? reader.result : c))
        )
      );
    };
    reader.readAsDataURL(file);
  };

  // Clear a cell (new)
  const handleClearCell = (row, col) => {
    setGrid((oldGrid) =>
      oldGrid.map((r, ri) =>
        r.map((c, ci) => (ri === row && ci === col ? null : c))
      )
    );
  };

  // PDF generation
  const generatePDF = () => {
    const doc = new jsPDF("p", "mm", "a4");
    const cellW = settings.cellWidth;
    const cellH = settings.cellHeight;

    grid.forEach((row, r) => {
      row.forEach((img, c) => {
        if (img) {
          let format = "PNG";
          if (img.toLowerCase().includes("jpeg") || img.toLowerCase().includes("jpg")) {
            format = "JPEG";
          }
          doc.addImage(
            img,
            format,
            settings.offsetX + c * (cellW + settings.colSpacing),
            settings.offsetY + r * (cellH + settings.rowSpacing),
            cellW,
            cellH
          );
        }
      });
    });
    return doc;
  };

  const handleDownloadPDF = () => {
    const doc = generatePDF();
    doc.save("labels.pdf");
  };

  const handlePrintPDF = () => {
    const doc = generatePDF();
    const pdfBlob = doc.output("bloburl");
    const printWindow = window.open(pdfBlob);
    printWindow.onload = () => {
      printWindow.focus();
      printWindow.print();
    };
  };

  // Save/load/delete profiles
  const saveProfile = () => {
    const name = prompt("Enter profile name:");
    if (!name) return;
    const newProfiles = {
      ...profiles,
      [name]: { settings, rows, cols },
    };
    setProfiles(newProfiles);
    localStorage.setItem("labelProfiles", JSON.stringify(newProfiles));
    setSelectedProfile(name);
  };

  const loadProfile = (name) => {
    const profile = profiles[name];
    if (!profile) return;
    setSettings(profile.settings);
    setRows(profile.rows);
    setCols(profile.cols);
    setSelectedProfile(name);
  };

  const deleteProfile = (name) => {
    if (!window.confirm(`Delete profile "${name}"?`)) return;
    const newProfiles = { ...profiles };
    delete newProfiles[name];
    setProfiles(newProfiles);
    localStorage.setItem("labelProfiles", JSON.stringify(newProfiles));
    if (selectedProfile === name) setSelectedProfile("");
  };

  // Center grid function
  const centerGrid = () => {
    const totalWidth =
      cols * settings.cellWidth + (cols - 1) * settings.colSpacing;
    const totalHeight =
      rows * settings.cellHeight + (rows - 1) * settings.rowSpacing;
    const newOffsetX = Math.round((settings.pageWidth - totalWidth) / 2);
    const newOffsetY = Math.round((settings.pageHeight - totalHeight) / 2);
    setSettings({ ...settings, offsetX: newOffsetX, offsetY: newOffsetY });
  };

  // Preview scaling
  const previewWidthPx = 600;
  const scale = previewWidthPx / settings.pageWidth;

  return (
    <div className="min-h-screen p-6 bg-gray-100">
      {/* Header with logo and title */}
      <div className="flex items-center justify-center relative mb-6">
        <img src={Logo} alt="Unica Logo" className="absolute left-0 h-12" />
        <h1 className="text-3xl font-bold text-center">Unica Label Generator</h1>
      </div>

      {/* Profile Controls centered */}
      <div className="flex justify-center gap-4 mb-6 items-center">
        <div className="flex items-center gap-2">
          <select
            value={selectedProfile}
            onChange={(e) => loadProfile(e.target.value)}
            className="border p-1 rounded"
            style={{ minWidth: 160 }}
          >
            <option value="">Select profile...</option>
            {Object.keys(profiles).map((name) => (
              <option key={name} value={name}>
                {name}
              </option>
            ))}
          </select>
          {selectedProfile && (
            <button
              className="ml-1 text-red-600 hover:text-red-800"
              title="Delete profile"
              onClick={() => deleteProfile(selectedProfile)}
            >
              ???
            </button>
          )}
        </div>
        <button
          onClick={saveProfile}
          className="px-3 py-1 bg-blue-600 text-white rounded"
        >
          Save Profile
        </button>
      </div>

      {/* Settings */}
      <div className="grid grid-cols-2 gap-4 mb-6 max-w-lg mx-auto">
        {[
          ["Page Width", "pageWidth"],
          ["Page Height", "pageHeight"],
          ["Offset X", "offsetX"],
          ["Offset Y", "offsetY"],
          ["Row Spacing", "rowSpacing"],
          ["Col Spacing", "colSpacing"],
          ["Cell Width", "cellWidth"],
          ["Cell Height", "cellHeight"],
          ["Rows", "rows"],
          ["Columns", "cols"],
        ].map(([label, key]) => (
          <div key={key}>
            <label className="block text-sm">{label}</label>
            <input
              type="number"
              value={key === "rows" ? rows : key === "cols" ? cols : settings[key]}
              onChange={(e) => {
                const val = Number(e.target.value);
                if (key === "rows") setRows(val);
                else if (key === "cols") setCols(val);
                else setSettings({ ...settings, [key]: val });
              }}
              className="w-full border p-1 rounded"
              min={key === "rows" || key === "cols" ? 1 : undefined}
            />
          </div>
        ))}
        {/* Center grid button */}
        <div className="col-span-2 flex justify-center mt-2">
          <button
            className="px-3 py-1 bg-gray-700 text-white rounded shadow"
            onClick={centerGrid}
          >
            Center Grid
          </button>
        </div>
      </div>

      {/* Preview */}
      <div
        className="relative border bg-gray-200 mx-auto"
        style={{
          width: `${settings.pageWidth * scale}px`,
          height: `${settings.pageHeight * scale}px`,
        }}
      >
        {grid.map((row, r) =>
          row.map((cell, c) => (
            <div
              key={`${r}-${c}`}
              onClick={() =>
                document.getElementById(`file-${r}-${c}`).click()
              }
              className="absolute border border-gray-400 flex items-center justify-center cursor-pointer bg-white overflow-hidden group"
              style={{
                width: `${settings.cellWidth * scale}px`,
                height: `${settings.cellHeight * scale}px`,
                left: `${(settings.offsetX + c * (settings.cellWidth + settings.colSpacing)) * scale}px`,
                top: `${(settings.offsetY + r * (settings.cellHeight + settings.rowSpacing)) * scale}px`,
              }}
            >
              {cell ? (
                <>
                  <img
                    src={cell}
                    alt="preview"
                    style={{
                      width: "100%",
                      height: "100%",
                      objectFit: "contain",
                    }}
                  />
                  {/* Clear button shown at top right of cell */}
                  <button
                    className="absolute top-0 right-0 m-1 text-xs bg-white bg-opacity-80 rounded-full px-1 py-0.5 shadow hover:bg-red-100 z-10"
                    title="Clear image"
                    tabIndex={-1}
                    onClick={(e) => {
                      e.stopPropagation();
                      handleClearCell(r, c);
                    }}
                  >
                    ?
                  </button>
                </>
              ) : (
                <span className="text-xs text-gray-500">Click to add</span>
              )}
              <input
                type="file"
                id={`file-${r}-${c}`}
                accept="image/*"
                className="hidden"
                onChange={(e) => handleFileChange(r, c, e)}
              />
            </div>
          ))
        )}
      </div>

      {/* Actions */}
      <div className="mt-6 flex gap-4 justify-center">
        <button
          onClick={handleDownloadPDF}
          className="px-4 py-2 bg-blue-600 text-white rounded shadow"
        >
          Download PDF
        </button>
        <button
          onClick={handlePrintPDF}
          className="px-4 py-2 bg-green-600 text-white rounded shadow"
        >
          Print
        </button>
      </div>
    </div>
  );
}